@import url(./reset.css);

body {
    background-color: rgb(21 24 29);
    color: #eee;
}

input :focus {
    outline: none;
    border: solid 0.2px;
}

.App {
    text-align: center;
    width: 100%;
    font-family: sans-serif;
}
/* 헤더 시작!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! */
header {
    background-color: #1c1f26;
    border-bottom: solid 2px #2b313a;

    box-shadow: 4px 6px 11px -5px #000000;

    margin-bottom: 20px;
}

.navBox {
    padding: 16px 0;
    width: 700px;
    margin: 0 auto;
    text-decoration: none;

    box-sizing: border-box;
}

.navBoxSmall {
    margin-right: 30px;
}

a {
    text-decoration: none;
    color: inherit;
}

.mainLogo {
    font-size: 35px;
    font-weight: bold;
    width: 700px;
    margin: 25px auto 0;
    position: relative;
}

.kokoimg {
    background-image: url('./img/대지\ 3.png');
    background-size: contain;
    width: 35px;
    height: 35px;
    position: absolute;
    top: 0;
    left: 200px;
}
/* 헤더 끝!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! */

/* 검색박스 시작!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! */

.searchBarBox {
    margin: 0 auto;
    width: 700px;
    position: relative;

    box-shadow: 4px 6px 11px -5px #000000;
}
.searchBar {
    border: solid 0.2px;
    border-radius: 10px;
    border-color: #707f96;
    height: 35px;
    width: inherit;
    background-color: #343a46;
    color: #eee;
    padding-inline: 0;

    text-align: center;
}

.searchBotton {
    position: absolute;
    right: 2px;
    top: 7px;
    cursor: pointer;
    background-color: rgb(21 24 29);
    color: #eee;
    width: 50px;
    box-shadow: 4px 6px 11px -5px #000000;

    border: solid 1px;
    border-radius: 15px;
    border-color: #2b313a;
}
/* 검색박스 끝!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! */

/* 캐릭터박스 시작!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! */

.chaBox {
    background-color: #1c1f26;

    width: 700px;
    margin: 30px auto;
    padding: 15px 15px;
    box-sizing: border-box;
    box-shadow: 4px 6px 11px -5px #000000;

    border: solid 1px;
    border-radius: 12px;
    border-color: #2b313a;

    text-align: start;
}

.smallChaTitle {
    font-size: 18px;
    font-weight: bold;
    display: inline-block;
    margin-bottom: 15px;
    margin-right: 15px;
    padding: 5px;

    height: 27px;
    line-height: 27px;
    text-align: center;

    border-radius: 5px;
    color: #c9c9c9;

    background-color: #2b313a;
}

.nameof {
    font-weight: bold;
    font-size: 2rem;
}

.title {
    display: inline-block;
    margin-top: 10px;
    font-size: 15px;
    font-weight: bold;
    color: #c9c9c9;
}

.chaImg {
    width: 370px;
    border: solid 1px;
    border-radius: 15px;
    border-color: #2b313a;
    margin-top: 15px;

    box-shadow: 4px 6px 11px -5px #000000;
}

.oneJ {
    font-weight: bold;
    margin-top: 15px;
    margin-left: 10px;
    padding: 10px;
    box-sizing: border-box;
    width: 280px;

    box-shadow: 4px 6px 11px -5px #000000;

    border: solid 1px;
    border-radius: 15px;
    border-color: #2b313a;

    display: flex;
    flex-direction: column;
}

.oneJNume {
    margin-bottom: 10px;
    display: inline-block;
    font-weight: bold;
    color: #c9c9c9;
}

.chaimgInfoFlexBox {
    display: flex;
    flex-wrap: wrap;
}

.exactInfoTitle {
    border-radius: 5px;
    color: #c9c9c9;

    margin-right: 5px;

    background-color: #2b313a;
    display: inline-block;
    padding: 5px;
}
.equBox {
    background-color: #1c1f26;

    margin: 50px 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    box-shadow: 4px 6px 11px -5px #000000;

    width: 700px;
    height: 345px;
    margin: 0 auto 30px;
    justify-content: space-evenly;
    align-items: center;
    border: solid 1px;
    border-radius: 12px;
    border-color: #2b313a;

    font-weight: bold;
}

.cardImg {
    background-image: url('https://cdn-lostark.iloa.gg/2018/obt/assets/images/pc/profile/img_card_grade.png');
    background-size: cover;
}
.cardsContainer {
    width: 700px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
.equBoxForCards {
    background-color: #1c1f26;

    margin: 50px 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    box-shadow: 4px 6px 11px -5px #000000;

    width: 700px;
    margin: 0 auto 30px;
    justify-content: space-evenly;
    align-items: center;
    border: solid 1px;
    border-radius: 12px;
    border-color: #2b313a;

    font-weight: bold;
}
.cardTitleBox {
    width: 700px;
    text-align: start;
    margin: 15px 0;
}
.cardTitle {
    font-size: 18px;
    padding: 5px;
    margin-left: 32px;

    height: 27px;
    line-height: 27px;
    border-radius: 5px;
    color: #c9c9c9;

    background-color: #2b313a;
}
.cardsBox {
    width: 80px;
    height: 120px;
    background-color: red;
    margin-bottom: 15px;
    position: relative;
}
.cardsBox > img {
    width: 80px;
    height: 120px;
}
.cardStarHiddenBox {
    position: absolute;
    width: 69px;
    height: 20px;
    overflow: hidden;

    bottom: 5px;
    left: 6px;

    z-index: 1000;
}
.cardStar {
    position: absolute;
    width: 69px;
    height: 20px;
    background-image: url(https://cdn-lostark.game.onstove.com/2018/obt/assets/images/pc/profile/img_profile_awake.png);
    background-size: cover;
    overflow: hidden;

    z-index: 10;
}
.cardStarGlow {
    position: absolute;
    width: 69px;
    height: 20px;
    background-image: url(https://cdn-lostark.game.onstove.com/2018/obt/assets/images/pc/profile/img_profile_awake.png);
    background-size: cover;
    background-position: 0 100%;
    overflow: hidden;

    z-index: 100;
}
.glow0 {
    right: 100%;
}
.glow1 {
    right: 80%;
}
.glow2 {
    right: 60%;
}
.glow3 {
    right: 40%;
}
.glow4 {
    right: 20%;
}
.glow5 {
    right: 0;
}
.legendCard {
    background-image: url(https://cdn-lostark.iloa.gg/2018/obt/assets/images/pc/profile/img_card_grade.png);
    background-position: 80.15% 0;
    position: absolute;
    background-size: cover;
    width: 86px;
    height: 126px;
    left: -3px;
    top: -3px;
}
.purpleCard {
    background-image: url(https://cdn-lostark.iloa.gg/2018/obt/assets/images/pc/profile/img_card_grade.png);
    background-position: 60.05% 0;
    position: absolute;
    background-size: cover;
    width: 86px;
    height: 126px;
    left: -3px;
    top: -3px;
}
.blueCard {
    background-image: url(https://cdn-lostark.iloa.gg/2018/obt/assets/images/pc/profile/img_card_grade.png);
    background-position: 39.95% 0;
    position: absolute;
    background-size: cover;
    width: 86px;
    height: 126px;
    left: -3px;
    top: -3px;
}
.greenCard {
    background-image: url(https://cdn-lostark.iloa.gg/2018/obt/assets/images/pc/profile/img_card_grade.png);
    background-position: 19.85% 0;
    position: absolute;
    background-size: cover;
    width: 86px;
    height: 126px;
    left: -3px;
    top: -3px;
}
.itemBoxSmall {
    display: flex;
    height: 45px;
    width: 190px;
    margin-bottom: 5px;
    text-align: left;
    position: relative;
}

.itemBoxSmall:nth-child(n + 13) {
    display: none;
}

.itemBoxSmall > span,
.itemBoxSmall > img {
    display: block;

    box-shadow: 4px 6px 11px -5px #000000;

    width: 50px;
    height: 45px;
    border: 1px;
    border-color: #2b313a;
}
.itemBoxSmall > img {
    border: solid 1px;
    border-radius: 5px;
    border-color: #495261;
    justify-content: flex-start;
    background-color: rgb(21 24 29);
    display: inline-block;
    margin-right: 7px;
}

.itemBoxSmall:nth-child(n + 7) {
    width: 160px;
}

.itemLevel {
    border: solid 1px;
    border-radius: 5px;
    border-color: #495261;

    width: 42px;
    height: 23px;
    font-size: 15px;
    line-height: 23px;
    display: inline-block;
    text-align: center;
    margin-left: 5px;

    box-shadow: 4px 6px 11px -5px #000000;
}

.purple {
    background-color: purple;
    width: 32px;
    height: 23px;
    font-size: 15px;
    line-height: 23px;
    display: inline-block;
    text-align: center;
    border-radius: 7px;
    right: 0;
    position: absolute;
}

.blue {
    background-color: #1260eb;
    width: 32px;
    height: 23px;
    font-size: 15px;
    line-height: 23px;
    display: inline-block;
    text-align: center;
    border-radius: 7px;
    right: 0;
    position: absolute;
}

.green {
    background-color: green;
    width: 32px;
    height: 23px;
    font-size: 15px;
    line-height: 23px;
    display: inline-block;
    text-align: center;
    border-radius: 7px;
    right: 0;
    position: absolute;
}

.yellow {
    background-color: rgb(177, 177, 0);
    width: 32px;
    height: 23px;
    font-size: 15px;
    line-height: 23px;
    display: inline-block;
    text-align: center;
    border-radius: 7px;
    right: 0;
    position: absolute;
}

.orange {
    background-color: #ea6911;
    width: 32px;
    height: 23px;
    font-size: 15px;
    line-height: 23px;
    display: inline-block;
    text-align: center;
    border-radius: 7px;
    right: 0;
    position: absolute;
}

.red {
    background-color: #a70303;
    width: 32px;
    height: 23px;
    font-size: 15px;
    line-height: 23px;
    display: inline-block;
    text-align: center;
    border-radius: 7px;
    right: 0;
    position: absolute;
}

/* 캐릭터박스 끝!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! */

/* 이벤트박스 시작!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! */
.eventContainer {
    width: 700px;
    margin: 0 auto;
    margin-top: 50px;
}
.eventsBoxBig {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.eventsName {
    width: 700px;
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 20px;
    width: 170px;
    height: 40px;
    line-height: 40px;
    text-align: center;

    border-radius: 5px;

    color: #c9c9c9;

    background-color: #2b313a;
}
.eventsBox {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-evenly;

    margin-top: 20px;

    border-radius: 5px;
}

.eventsBoxSmall {
    display: flex;
    flex-direction: row;
    align-items: center;

    width: 345px;

    font-size: 15px;

    margin-bottom: 5px;

    box-shadow: 4px 6px 11px -5px #000000;

    border: solid 1px;
    border-radius: 12px;
    border-color: #2b313a;

    color: #c2c2cc;
}

.eventsSpan {
    margin-left: 15px;

    font-weight: bold;
}

.eventsImg {
    width: 50px;
    display: block;
    border-radius: 5px;

    border: solid 2px;
    border-radius: 5px;
    border-color: #495261;
}

.smallTitle {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;

    width: 45px;
    height: 22px;
    line-height: 22px;
    text-align: center;

    border-radius: 5px;
    color: #c9c9c9;

    background-color: #2b313a;
}

@media all and (max-width: 768px) {
    body {
        font-size: 0.89rem;
    }
    .mainLogo {
        width: 410px;
    }
    .searchBarBox {
        width: 410px;
    }
    .chaBox {
        width: 410px;
    }

    .equBox {
        width: 410px;
    }
    .eventsBox,
    .eventsName,
    .navBox,
    .equBoxForCards,
    .cardTitleBox {
        width: 410px;
    }
    .cardTitle {
        margin-left: 38px;
    }
    .cardsContainer {
        width: 410px;
        padding: 5px;
        box-sizing: border-box;
    }
    .cardsBox {
        margin: 12px;
    }
    .eventContainer {
        width: 410px;
    }
    .eventsBoxSmall {
        width: 100%;
    }
    .eventsBoxBig {
        flex-direction: column;
    }

    .itemBoxSmall {
        width: 180px;
    }

    .chaimgInfoFlexBox {
        flex-direction: column;
        align-items: center;
    }
    .oneJ {
        width: 370px;
        margin-left: 0;
    }
}
